
@font-face { font-family: Delicious; src: url(/static/media/gothic.09f6b2ba.ttf); } 


*{
    font-family: Delicious !important;
}
.navbar{
    padding-top: 50px;
}
.navbar ul li{
    margin-left: 20px;
}
.navbar ul li a{
    font-size: 13px;
    border: 1px solid transparent;
     padding: 15px 25px 15px 25px !important;
}
.navbar ul li a:hover{
    border: 1px solid #C6BD7D;
    transition : 0.5s;
    color : #C6BD7D !important;
   
}
*{
    color : #b7b7b7;
}

.brand{
    width: 210px;
    height : 100px;
    cursor: pointer;
}

.works{
    height: 100%;
    background: black;
   
}
.top {
    padding-top: 150px;
}
.active{
    border: 1px solid #C6BD7D;
    color : #C6BD7D !important;
}
h2{
    font-weight: 100 !important;
}

.resume{
    color: #C6BD7D;
    border : 2px solid #C6BD7D;
    background: none;
    padding : 5px 10px 5px 10px ;
}
.about{
    background: rgba(0,0,0,0.5);
    height : 100%;
    padding-bottom: 100px;
}

.primary {
    color :#C6BD7D !important;
}

body{
    /*background: linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.5)),url('./img/lights.gif');*/
    background-position: center;
    background: linear-gradient(rgba(0,0,0,0.6),rgba(0,0,0,0.6)),url(/static/media/bird.b55f9433.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    background-attachment: fixed;
    
}
.footers{
    background: black;
}

#map{
    width:"400px";
    height:"240px";
    frameborder:"0";
    border:0
}

@media(max-width : 786px){
    #works{
        display: grid;
        justify-content: center;
        align-items: center;
        
    }
    .footers{
        padding-top: 60px;
    }
    .navbar-brand{
        display: none !important;
    }
    #map{
        width: 100%;
    }
    
    .work_icons{
        text-align: center;
        
    }
    h1 ,h5 ,h2 ,h3 ,h4{
        font-size: 20px;
        font-weight: 100 !important; 
    }
    p{
        font-size: 13px;
    }
    #home{
        text-align: left;
        padding: 0px 10px;
        margin-top: -60px;
    }
    button{
        font-size: 15px;
    }
    body:before {
  content: "";
  display: block;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -10;
  background: linear-gradient(rgba(0,0,0,0.6),rgba(0,0,0,0.6)),url(/static/media/bird.b55f9433.jpg);
  background-size: cover;
}
   
    .what_i_do{
        padding-top: 50px;
    }
    #works{
        display: grid !important;
        align-items: center !important;
        justify-content: center !important;
    }
    .navbar{
    padding: 10px 0px 10px 0px;
    background: rgba(0,0,0,0.5);
    
    }
    .navbar ul{
        background: none !important;
        margin-top: 10px;
    }
    .navbar a{
        background: none !important;
    }
    .navbar li{
        background: none !important;
    }
    
    
    
    
    
    
.navbar ul li{
    margin-left: 0px;
}
.navbar ul li a{
    border: 1px solid transparent;
     padding: 0px;
    background: rgba(0,0,0,0.5);
}
.navbar ul li a:hover{
    border: 1px solid #C6BD7D;
    transition : 0.5s;
    color : #C6BD7D !important;
   
   
}
}
